.allcomment{
    border: 1px solid black;
    padding: 6px;
    background:#c7c7c7;
    width: 250px;
    margin-bottom: 20px;
    border-radius: 30px;
}
.comment p {
    margin: 8px;
    font-style: italic;
}
.avatar img {
    width: 70px;
    height: 60px;
    border-radius: 50px;
    margin-right:10px;
}

.avatar{
    display: flex;
    font-weight: 700;
    font-size: 19px;
}
.image{
    text-align:center;
    margin-bottom: 30px;
}
.image img{
    width:90%;
    height: 300px;
}

.like img{
    width: 20px;
    height: 20px;
}

.like{
    vertical-align: middle;
    display: flex;
    margin-bottom: 5px;

}
.likep{
    margin-left:4px;
    font-size: 18px;
    font-weight: 500;
}